.modal {
    background-color:rgba(0,0,0,0.6);
}
.modal-dialog-div{
    padding: 20px;
    background: #fff;
    color: #000;
    border: 1px solid #ccc;
    font-size: 18px;
    -webkit-box-shadow: 0px 1px 5px 1px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0px 1px 5px 1px rgba(0, 0, 0, 0.3);
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.7);
    border-radius: 10px;
    pointer-events: all;
    max-height: 800px;
    margin: 1.75rem auto;
    overflow-y: scroll;
}
.modal-dialog-success  {
    color: #00AC3E !important;
}
.modal-dialog-warning  {
    color: #FFBC3D !important;
}
.modal-dialog-error  {
    color: #ED7000 !important;
}
.modal-dialog-div .btn{
    border-radius: 50px;
    width: 90px;
}
i.fa{
    background: #989898;
    color: #ffffff;
    height: 40px;
    width: 38px;
    border-radius: 50px;
    line-height: 40px;
    font-size: 20px;
    text-align: center;
}

.sub-head{
    color: #0088CE;
}
