/* Loader CSS */
.spinnerWrapper{
    width: -webkit-fill-available;
    height: -webkit-fill-available;
    background-color: #000;
    opacity: 0.5;
    position: fixed;
    z-index: 999;
    top: 0;
    bottom: 0;
    text-align: center;
}
.spinner{
    position: fixed !important;
    top: 30% !important;
    z-index: 1000 !important;
    color: #fff !important;
}

/* Form buttons CSS */
.primary{
    background-color: #000000 ;
    color: #ffffff;
    border-radius: 30px;
    padding: 7px 15px;
    border: 0px;
}
.secondary{
    background-color: #ffffff;
    color: #000000;
    border-radius: 30px;
    padding: 7px 15px;
    border:1px solid #000000;
}
button:disabled{
    background-color: #5e5c5c !important;
    cursor: not-allowed;
}

 /* Header & Navigation Bar CSS */
.navbar{
    background-color: #ffffff!important;
    border-bottom:1px solid #d8dada;
    box-shadow: 0px 0px 7px 0px;
}
.navbar-expand-lg .navbar-toggler{
    display: block !important;
}
.brand, a.brand.active:hover, a.brand:hover{
    font-weight:bold;
    color:#d52b1e;
    font-size:20px;
    text-decoration: none;
}
#userProfileIcon{
    border-radius: 50px;
    width: 50px;
    height: 50px;  
}
a#basic-nav-dropdown{
    color:#000000;
}
#mySidenav{
    width: 250px;
}
.sidenav {
    height: 100%; 
    width: 0; 
    position: fixed; 
    z-index: 1; 
    top: 56px;
    left: 0;
    background-color: #111; 
    overflow-x: hidden; 
    transition: 0.5s; 
}
.sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 18px;
    color: #ffffff;
    display: block;
    transition: 0.3s;
}
.sidenav a:hover {
    color: #818181;
    text-decoration: none;
}
.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}
@media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
}